.NavBar{
    background-color: #181818;
    height: 80px;
    width: 100%;
    position: fixed;
    z-index: 999;
    opacity: 0.9;
    display: flex;
    justify-content: center;
    align-items: center;

}
.NavBar nav ul {
list-style: none;
margin: 0;
padding: 0;

}
.NavBar nav ul li{
    display: inline-block;
    margin: 20px;
}
.NavBar nav ul li a{
    text-decoration: none;
    color: white;
    
}
.NavBar nav ul li a:hover{
    border-bottom: 5px solid #129799;
}
